.singleShare {
  position: absolute;
  left: calc(100% - 50% - 543px);
}
.containerSingleShare {
  top: 109px;
  margin: 20px;
  border-radius: 4px;
  overflow: visible;
  padding: 14px 20px;
  width: 1086px;
  margin: 20px auto;
  font-family: "Poppins", sans-serif;

  .arrow-heading-wrapper {
    display: flex;
    align-items: center;
    padding-bottom: 20px;
    // border-bottom: 1px solid #ddd;
    margin: 20px 0 0 0px;
  }
  .arrow-div {
    background: #21745d1c;
    padding: 5px 10px;
    border-radius: 4px;
    display: flex;
    justify-content: center;
    margin-right: 10px;
    cursor: pointer;
  }
  .score-details-heading {
    font-size: 16px;
    font-weight: 700;
  }

  .table-container {
    margin: 5px 0px 20px;

    .resume-btn {
      color: #04724d;
      font-weight: 700;
      text-align: center;
      padding-left: 20px;
      font-family: "Poppins", sans-serif;
      font-size: 11px;
      display: flex;
      justify-content: center;
      cursor: pointer;
    }
    .resume-btn-disabled {
      color: #ccc;
      text-align: center;
      padding-left: 20px;
      font-weight: 700;
      font-family: "Poppins", sans-serif;
      font-size: 11px;
      display: flex;
      justify-content: center;
    }
  }

  .heading {
    display: flex;
    justify-content: space-between;
    .summary-heading {
      font-size: 16px;
      font-weight: 700;
    }
    .total-marks {
      text-transform: uppercase;
      font-size: 16px;
      font-weight: 700;
    }
  }
  .details-container-header {
    margin: 20px 0;
    padding: 12px;
    background: #e9f3ef;
    border-radius: 5px;
    font-size: 14px;
    font-weight: 700;
    display: flex;
    justify-content: space-between;
    .marks-container {
      display: flex;
      justify-content: space-around;

      .total-marks-body {
        margin: 0 15px;
        text-transform: uppercase;
        font-size: 14px;
        font-weight: 600;
      }
    }
  }
  .details-container-body {
    background: #f1f3f6;
    margin: 20px 0;
    padding: 12px;
    border-radius: 5px;
    font-size: 14px;

    .tooltip-container {
      border-radius: 50%;
      margin-left: 3px;
      padding: 0px 3.5px;
      width: 20px;
      height: 20px;
      background: #c8a66e;
      .question-icon {
        color: #ffffff;
        font-size: small !important;
        margin-top: -3px;
      }
    }
    .verbal-heading-container {
      font-weight: 700;
      display: flex;
      justify-content: flex-start;
      margin-top: 10px;

      .verbal-heading-question {
        width: 765px;
        margin-left: 10px;
      }
      .verbal-heading-rating {
        padding: 0 10px;
        width: 15%;
      }
      .verbal-heading {
        padding: 0 10px;
        width: 120px;
        display: flex;
      }
    }
    .verbal-body-container1 {
      margin-top: 20px;
    }
    .mcq-heading-container {
      font-weight: 700;
      display: flex;
      justify-content: flex-start;
      margin-top: 10px;

      .mcq-heading-question {
        width: 720px;
        display: flex;
        margin-left: 10px;
      }
      .mcq-heading {
        // padding: 0 10px;
        display: flex;
        width: 130px;
      }
    }
    .mcq-body-container1 {
      margin-top: 20px;
      font-weight: 600;
      .mcq-body-container {
        display: flex;
        justify-content: flex-start;

        .mcq-body {
          padding: 0 10px;
          width: 120px;
          background: #fff;
          border-radius: 5px;
          padding: 12px;
          margin: 7px 7px;
          max-height: 50px;
          text-align: center;
          font-size: 13px;
        }
      }
    }
  }
}

.status-selectSingle {
  margin-top: -5px;

  [class$="-control"],
