  }
  [class$="-placeholder"] {
    font-size: 10px;

    // text-transform: lowercase;
  }
}
.share-btn1 {
  display: flex;
  justify-content: center;
}
.resume-modal-Single {
  border-radius: 10px;
  outline: none;
  background-color: white;
  width: 500px;
  height: 700px;
  margin: auto;
  justify-content: center;
  align-items: center;
  font-family: Poppins;
  font-size: 16px;
  margin-top: 25px;
  bottom: 10px;
  .pg-viewer-wrapper,
  .pg-viewer-wrapper .document-container {
    width: calc(500px - 40px);
    height: calc(700px - 40px);
  }
}
.VideoPlayer {
  border-radius: 10px;
  outline: none;
  background-color: white;
  // height: 500px;
  // width: 500px;
  margin: auto;

  font-family: Poppins;
  font-size: 16px;
  position: absolute;
  top: calc(100% - 50% - 270px);
  left: calc(100% - 50% - 316px);
}
.ShareModalSingle {
  .shareMainDiv {
    width: 60%;
    position: absolute;
    top: 20%;
    left: calc(100% - 50% - 30%);
    .titlee {
      color: #1e1e1e;
      opacity: 1;
      font-weight: bold;
      font-size: 15px;
    }
    .shareLink {
      background: #ffffff 0% 0% no-repeat padding-box;
      border-radius: 8px;
      opacity: 1;
      // padding: 15px 20px;
      font-family: "Poppins", sans-serif;

      .top {
        display: flex;
        justify-content: space-between;
        padding: 15px 20px 0;

        img {
          width: 24px;
          height: 24px;
        }
      }
      .line {
        border-bottom: #707070 1px solid;
        margin: 10px 0;
      }
      .linkArea {
        padding: 0 20px 15px;
        .LinkTitle {
          color: #000000;
          font-size: 17px;
          font-weight: 600;
        }
        .LinkSubTitle {
          color: #000000;
          font-size: 11px;
          font-weight: 500;
          padding: 10px 0;
        }
      }
    }
    .shareEmail {
      background: #ffffff 0% 0% no-repeat padding-box;
      border-radius: 8px;
      opacity: 1;
      padding: 15px 20px;
      margin-top: 10px;
      height: 155px;
      .multiselectEmail {
        margin-top: 5px;
        .skillsAuto .MuiInputBase-root {
          padding: 0 10px;
          display: flex;
          justify-content: center;
          align-items: center;
          height: auto !important;
          min-height: 34px !important;
          width: 100% !important;
          margin: 0 !important;
          border: 2px solid #ddd;
          border-radius: 4px;
          background: #fff;
        }

        .skillsAuto .MuiChip-outlined {
          border: 1px solid #333;
          color: #fff;
          background: black;
          height: 20px;
          margin: 5px;
        }
        .MuiChip-outlined .MuiChip-deleteIcon {
          color: #fff;
        }
      }
    }
  }
  .send-response-div {
    padding-top: 20px;
    float: right;

    .send-response-button {
      border-radius: 4px !important;
    }
  }
}

.score-tooltip-main {
  font-size: 9px;
  width: 150px;

  .main-wrapper-score {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 5px 0;
  }

  .score-heading {
    font-weight: bold;
    width: 33%;
  }

  .score-content {
