.resumeIconSingle {
  margin-right: 4px;
  cursor: pointer;
  background-color: #e6ecf6;
  border-radius: 50%;
  padding: 0 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  svg {
    width: 15px;
  }
}
.CommentModalSingle {
  .MuiCardContent-root {
    padding: 0;
  }
  border-radius: 4px;
  outline: none;
  background-color: white;
  width: 450px;
  height: 500px;
  bottom: 10px;
  right: 10px;
  position: absolute;
  .AreaComment {
    position: relative;
    .headerComment {
      background-color: #d5e5e3;
      padding: 15px 20px;
      display: flex;
      justify-content: space-between;
      text-align: left;
      font-weight: 700;
      font-size: 13px;
      letter-spacing: 0px;
      color: #21745d;
      opacity: 1;
      margin-bottom: 10px;
      img {
        cursor: pointer;
      }
    }
    .NewCommentSection {
      height: 150px;
      bottom: -170px;
      width: calc(100% - 20px);
      margin: 0 10px 10px 10px;
      background-color: #f1f3f6;
      padding: 10px;
      position: absolute;
      border-radius: 10px;
      .commentTextArea {
        border: 1px solid #d8d8d8;
        border-radius: 6px;
        width: 100%;
        outline: none;
      }
      .sendComment {
        display: flex;
        justify-content: flex-end;
        margin-top: 10px;
      }
    }
    .OldCommentSection {
      height: calc(450px - 130px - 55px);
      overflow: auto;
      font-family: "Poppins", sans-serif;
      .oldcommentText {
        background: #ffffff;
        border: 1px solid #d8d8d8;
        border-radius: 5px;
        opacity: 1;
        margin: 5px 10px;
        padding: 15px 20px;
        .userName {
          text-align: left;
          font-weight: 700;
          font-size: 11px;
          letter-spacing: 0px;
          color: #000000;
          opacity: 1;
        }
        .timeComment {
          text-align: left;
          font-weight: 400;
          font-size: 9px;
          letter-spacing: 0px;
          color: #363636;
          opacity: 1;
          margin: 2px 0 10px 0;
        }
        .noteComment {
          text-align: left;
          font-weight: 400;
          font-size: 11px;
          letter-spacing: 0px;
          color: #000000;
          opacity: 1;
        }
      }
    }
  }
}
