.verbal-body-container {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;

  .verbal-body-question {
    width: 700px;
    background: #fff;
    border-radius: 5px;
    padding: 10px;
    margin: 7px 7px;
    font-size: 11px;
    max-height: 200px;
    min-height: 40px;
    overflow: auto;
    cursor: pointer;
  }
  .verbal-body-rating {
    padding: 0 10px;
    width: 15%;
    background: #fff;
    border-radius: 5px;
    margin: 7px 7px;
    font-size: 13px;
    text-align: center;
  }
  .verbal-body {
    padding: 0 10px;
    width: 120px;
    background: #fff;
    border-radius: 5px;
    padding: 10px;
    margin: 7px 7px;
    font-size: 13px;
    font-weight: 600;
    text-align: center;
  }
}
