.ReactTable {
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
}

.ReactTable .rt-th {
  padding: 11px 5px;
}

.ReactTable .rt-thead .rt-th {
  padding: 10px 5px !important;
}

.ReactTable .rt-tbody .rt-td {
  height: 34px;
  color: #000;
  font-weight: unset;
  font-family: "Poppins", sans-serif;
  font-size: 11px;
}
.ReactTable .-pagination {
  display: none !important;
}
.ReactTable .-pagination .-btn {
  display: none;
}

.ReactTable .-sort-desc,
.ReactTable .-sort-asc {
  box-shadow: none !important;
}
.ReactTable .-sort-desc:before {
  content: "▼";
  float: right;
}

.ReactTable .-sort-asc:before {
  content: "▲";
  float: right;
}

.ReactTable .rt-thead .rt-resizable-header-content {
  color: #000;
  font-size: 11px;
  font-weight: 700;
  padding-left: 11px;
  font-family: "Poppins", sans-serif;
}
.ReactTable .rt-thead .rt-resizable-header-content:first-child {
  padding-left: 0;
}

.ReactTable .rt-thead .rt-tr {
  background: #e6ecf6 !important;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
}

.ReactTable .rt-tr-group {
  background: #fff;
  border: 1px solid #eef0f2 !important;
}

.ReactTable .rt-tr-group:nth-of-type(even) {
  background: #eef0f2;
}

.ReactTable .rt-thead .rt-th.-cursor-pointer {
  height: 40px;
  display: flex;
  align-items: center;
  font-family: "Poppins", sans-serif;
}
.paginationTopContainer {
  display: flex;
  justify-content: space-between;
  height: 60px;
  margin: 10px 0px;
  padding: 0 10px 0 5px;
  background: #e6ecf6;
  border-radius: 4px;
}
.options-wrapper {
  padding-top: 16px;
  padding-bottom: 16px;
}
.refreshIcon-container {
  background: #21745d1c;
  border-radius: 5px;
  margin-left: 10px;
  cursor: pointer;
}
.pagination-wrapper {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  padding-top: 16px;
  padding-bottom: 16px;
  align-items: center;
}

.shows-rows {
  color: #000000;
  font-size: 12px;
  font-weight: 700;
  display: flex;
  align-items: center;
  padding-right: 6px;
}

.range,
.range-box {
  border: 1px solid #b4b4b4;
  border-radius: 6px;
  padding: 2px 4px;
}

.range-box {
  border-top-right-radius: unset;
  border-bottom-right-radius: unset;
  border-right: none;
}

.goBtn {
  color: #21745d;
  font-size: 12px;
  font-weight: 700;
  border: 1px solid #b4b4b4;
  border-radius: 6px;
  padding: 2px 4px;
  border-left: none;
  border-top-left-radius: unset;
  border-bottom-left-radius: unset;
  cursor: pointer;
}

.arrow-btn,
.disable-btn {
  background-color: #04724d;
  border-radius: 4px;
  padding: 0px 4px;
  cursor: pointer;
}

.disable-btn {
  cursor: not-allowed;
  opacity: 0.4;
}

.selected-items-heading {
  padding: 10px 10px;
  color: #3e4042;
  font-size: 14px;
  margin-left: 20px;
}
.ReactTable .rt-thead .rt-th {
  outline: none;
}
.ReactTable .rt-resizer {
  z-index: 0 !important;
}
.selectRowCountDropDown {
  width: 50px;
  [class$="-control"] {
    max-height: 24px;
    min-height: 24px;
    border-radius: 6px;
  }
  [class$="-ValueContainer"] {
    padding: 2px 4px;
  }
  [class$="-singleValue"] {
    margin-left: 0px;
  }
  [class$="-indicatorContainer"] {
    padding: 0 0 10px 0 !important;
  }
  [class$="-indicatorSeparator"] {
    display: none;
  }
}
